import React from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { makeStyles } from "@material-ui/core/styles";
import NavWord from './NavWord'

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";

const useStyles = makeStyles({
    root: {
      flexGrow: 1,
      background: 'linear-gradient(45deg, #1f1f1f 30%, #545454 90%)',
      border: 0,
      borderRadius: 3,
      boxShadow: '0 3px 5px 2px rgba(5, 5, 5, .3)',
      color: 'white',
      height: '4%',
      padding: '0 0px',
    },
    logo: {
      width: 135,
      height: 43.54
    }
});

const NavbarInstance  = ({ navItems }) => {
    const classes = useStyles();
      
    return (
        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Navbar.Brand style={{width: "10%"}}>
            <Link to="/">
                <img
                  className={classes.logo}
                  src={
                    "https://upload.wikimedia.org/wikipedia/commons/a/a7/React-icon.svg"
                  }
                  alt="TLCJ Logo"
                />
              </Link>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto" style={{fontSize: "14pt"}} >
              {
                Object.keys(navItems).map(key => <Nav.Link href={`/${navItems[key]}`}>
                    <NavWord text={key} />
                </Nav.Link>)
              }
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    )
};

export default NavbarInstance;