import React, { useState, useEffect } from 'react';
import logo from './logo.svg';
import './App.css';

import Header from './components/Header'
import Home from './components/Home'
import About from './components/About'
import Music from './components/Music'
import Contact from './components/Contact'
import Events from './components/Events'
import Inspiration from './components/Inspiration'
import NavbarInstance from './components/NavbarInstance'

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

class App extends React.Component {
  constructor() {
    super()
    this.state = {
      style: {
        background: "linear-gradient(45deg, #1f1f1f 30%, #545454 90%)",
        color: "white",
        minHeight: "100vh",
        animation: "fade 1s ease-in"
      },
      background: 0,
      className: "fadein"
    }
  }
  backgroundStyles = [
    "linear-gradient(45deg, #1f1f1f 30%, #545454 90%)",
    "linear-gradient(45deg, #1f1f1f 30%, #121212 90%)", 
    "linear-gradient(45deg, #121fff 30%, #121212 90%)",
    "linear-gradient(45deg, #ee022e 30%, #abbbab 90%)",
    "linear-gradient(45deg, #11b0b0 30%, #12feed 90%)",
  ]
  
  change = () => {
    this.setState({
      style: { 
        ...this.state.style, 
        background: this.backgroundStyles[(this.state.background + 1) % this.backgroundStyles.length]
      },
      background: this.state.background + 1
    })
  }
  
  componentDidMount() {
    this.id = setInterval(() => {
        this.change()
      }, 3000)
  }
  componentWillUnmount() {
    clearInterval(this.id)
  }
  
  render() {
    // navigation entries mapping to its route
    const navMap = {
      'Home': 'home', 
      'Me': 'about', 
      'My music': 'music', 
      'Inspiration': 'inspiration', 
      'Events': 'events', 
      'Contact': 'contact'
    }
    return (
      <Router basename="/tlcj_music/">
        <div style={{background: this.state.style.background}}>
          <NavbarInstance navItems={navMap} />
        </div>
        
        <Switch>
          <Route path="/about">
            <About style={this.state.style} />
          </Route>
          <Route path="/music">
            <Music style={this.state.style} />
          </Route>
          <Route path="/inspiration">
            <Inspiration style={this.state.style} />
          </Route>
          <Route path="/events">
            <Events style={this.state.style} />
          </Route>
          <Route path="/contact">
            <Contact style={this.state.style} />
          </Route>
          <Route path={["/", "home"]} >
            <Home style={this.state.style} />
          </Route>
        </Switch>
      </Router>
    );
  }
  
}

export default App;
