import React, { useState } from "react";

const NavWord = ({ text }) => {
    const [hover, setHover] = useState(false)

    const toggleHover = () => {
        setHover(!hover)
    }

    const hoverStyle = {
        color: "white",
        paddingBottom: "3px",
        borderBottom: "1px solid white"
    }

    const plainStyle = {
        color: "white"
    }
    return (
        <span style={hover ? hoverStyle: plainStyle} onMouseEnter={toggleHover} onMouseLeave={toggleHover} >{text}</span>
    )
}

export default NavWord